// React API
import * as React from 'react'

import { generateNoScriptInfoDataStructure } from '../utils/helpers'

// Types
import { Feature } from '../types/featureTypes'

// Components
import NoScriptList from './noScriptList'

// Actions
import * as featureActions from '../actions/featureActions'

// Helpers
import { filterNoScriptInfoByBlockedState, getBlockAllText } from '../utils/helpers'

import {
  BlockedListHeader,
  BlockedListSummary,
  BlockedListContent,
  BlockedListItemHeader,
  BlockedListDynamic,
  BlockedListFooter,
  ArrowUpIcon,
  LinkAction,
  Favicon,
  SiteInfoText,
  BlockedListSummaryText,
  BlockedListItemHeaderStats,
  BlockedListItemHeaderText,
  FakeButton
} from '../styled-components'

interface Props {
  actions: typeof featureActions
  featureData: Feature.State
}

export default class CoreFeature extends React.PureComponent<Props, {}> {
  /**
   * DO NOT ADD THIS TO PROD
   *
   */
  removeLocalStorage = () => {
    window.localStorage.clear()
    window.location.reload()
  }

  get noScriptInfo () {
    return this.props.featureData.noScript
  }

  get actions () {
    return this.props.actions
  }

  get generatedNoScriptData () {
    return generateNoScriptInfoDataStructure(this.noScriptInfo)
  }

  get blockedScriptsLength () {
    return filterNoScriptInfoByBlockedState(Object.entries(this.noScriptInfo), true).length
  }

  get allowedScriptsLength () {
    return filterNoScriptInfoByBlockedState(Object.entries(this.noScriptInfo), false).length
  }

  blockOrAllowAll (blockOrAllow: boolean) {
    this.actions.setAllScriptsBlockedCurrentState(blockOrAllow)
  }

  setFinalScriptsBlockedState = () => {
    this.actions.setFinalScriptsBlockedState()
  }

  getBlockAllText (shouldBlock: boolean) {
    return getBlockAllText(this.noScriptInfo, shouldBlock)
  }

  render () {
    return (
      <div>
        <div style={{
          margin: '40px auto -20px',
          textAlign: 'center'
        }}>
          <button onClick={this.removeLocalStorage} style={{ fontSize: '20px' }}>reset persistent storage</button>
        </div>
        <BlockedListContent style={{
          width: '370px',
          height: 'max-content',
          boxShadow: '1px 1px 12px rgba(0,0,0,0.16)',
          margin: '60px',
          position: 'relative'
        }}>
          <BlockedListHeader>
            <Favicon src='' />
            <SiteInfoText>test.com</SiteInfoText>
          </BlockedListHeader>
          <details open={true}>
            <BlockedListSummary stats={false}>
              <ArrowUpIcon />
              <BlockedListSummaryText>Scripts blocked</BlockedListSummaryText>
            </BlockedListSummary>
            <BlockedListDynamic>
            {
              this.blockedScriptsLength > 0 && (
                <>
                  <BlockedListItemHeader id='blocked' style={{ zIndex: 1}}>
                    <BlockedListItemHeaderStats>
                      {this.blockedScriptsLength}
                    </BlockedListItemHeaderStats>
                    <BlockedListItemHeaderText>Blocked scripts</BlockedListItemHeaderText>
                    <LinkAction onClick={this.blockOrAllowAll.bind(this, false)}>{this.getBlockAllText(true)}</LinkAction>
                  </BlockedListItemHeader>
                  <NoScriptList shouldBlock={true} actions={this.actions} noScriptInfo={this.generatedNoScriptData} />
                </>
              )
            }
            {
              this.allowedScriptsLength > 0 && (
                <>
                  <BlockedListItemHeader id='allowed' style={{ zIndex: 1}}>
                    <BlockedListItemHeaderStats>
                      {this.allowedScriptsLength}
                    </BlockedListItemHeaderStats>
                    <BlockedListItemHeaderText>Allowed scripts</BlockedListItemHeaderText>
                    <LinkAction onClick={this.blockOrAllowAll.bind(this, true)}>{this.getBlockAllText(false)}</LinkAction>
                  </BlockedListItemHeader>
                  <NoScriptList shouldBlock={false} actions={this.actions} noScriptInfo={this.generatedNoScriptData} />
                </>
              )
            }
            </BlockedListDynamic>
          </details>
          <BlockedListFooter>
            <FakeButton onClick={this.setFinalScriptsBlockedState}>Cancel</FakeButton>
          </BlockedListFooter>
        </BlockedListContent>
      </div>
    )
  }
}
