import styled from 'brave-ui/theme'
import { BlockedListItemWithOptions } from '../styled-components'


export const BlockedListItemDetails = styled('details')`
  &[open] summary:after {
    content: "-";
  }
  position: relative;
`

export const BlockedListItemSummary = styled(BlockedListItemWithOptions.withComponent('summary'))`
  position: relative;
  &::-webkit-details-marker {
    display: none;
  }
  &:after {
    position: absolute;
    content: "+";
    top: 0;
    left: 0;
    color: black;
    font-size: 20px;
    padding: 6px 26px;
  }
`
