// Redux API
import { Reducer, AnyAction } from 'redux'

// Feature storage
import * as storage from '../storage'

// Types
import { Feature } from '../types/featureTypes'
import { types } from '../types/actionsTypes'
import { getHostname } from '../utils/helpers'

// State helpers
import { setNoScriptInfo, filterNoScriptInfoByBlockedState } from '../utils/helpers'

const featureReducer: Reducer<Feature.State | undefined> = (state: Feature.State | undefined, action: AnyAction) => {
  if (state === undefined) {
    state = storage.load()
  }

  const payload = action.payload
  const startingState = state
  const noScript = state.noScript

  // NoScriptInfo is the name we call for the list of scripts that are either
  // blocked or allowed by the user. Each script have three properties:
  // ....................................................................................
  // `actuallyBlocked`:
  // ....................................................................................
  // When set to `true` it blocks the script immediatelly. This is the initial state
  // when the user toggle scripts blocked in the main panel screen and also the initial state
  // for when users toggle `block/allow` or `block all/allow all`
  // ....................................................................................
  // `willBlock`:
  // ....................................................................................
  // When set to `true` it moves the script to its respective list. This is the final state
  // when the user choose to close Shields either by clicking `cancel`, moving back to the
  // main screen, or closing Shields browser action. This state is triggered only after those actions
  // and its state inherit the state of `actuallyBlocked`.
  // ....................................................................................
  // `userInteracted`:
  // ....................................................................................
  // This property is for display only. With this we can tell whether or not the user have
  // interacted with the script which can change the button state to allow/block (no user interaction)
  // or blocked once/allowed once (user has interacted).

  switch (action.type) {
    // Used for standalone scripts i.e. not grouped by other scripts in the same hostname
    case types.SET_SCRIPT_BLOCKED_ONCE_CURRENT_STATE:
      const scriptBlockedState = {
        userInteracted: true,
        actuallyBlocked: !noScript[payload.url].actuallyBlocked
      }
      state = setNoScriptInfo(state, payload.url, scriptBlockedState)
      break

    case types.SET_GROUPED_SCRIPTS_BLOCKED_ONCE_CURRENT_STATE:
      const maybeBlockGroup = payload.maybeBlock
      for (const [url] of filterNoScriptInfoByBlockedState(Object.entries(noScript), maybeBlockGroup)) {
        const groupedScriptsBlockedState = {
          userInteracted: true,
          actuallyBlocked: !noScript[url].actuallyBlocked
        }

        if (payload.hostname === getHostname(url)) {
          state = setNoScriptInfo(state, url, groupedScriptsBlockedState)
        }
      }
      break

    case types.SET_ALL_SCRIPTS_BLOCKED_ONCE_CURRENT_STATE:
      const maybeBlockAll = payload.maybeBlock
      for (const [url] of filterNoScriptInfoByBlockedState(Object.entries(noScript), maybeBlockAll, true)) {
        const groupedScriptsBlockedState = {
          userInteracted: true,
          actuallyBlocked: !noScript[url].actuallyBlocked
        }

        state = setNoScriptInfo(state, url, groupedScriptsBlockedState)
      }
      break

    case types.SET_FINAL_SCRIPTS_BLOCKED_ONCE_STATE:
      for (const [url] of Object.entries(noScript)) {
        // `willBlock` set the state once user close Shields panel moving blocked/allowed resources
        // to their respective lists. In this case we copy data from actuallyBlocked and apply
        // the sate state to willBlock so users can see their scripts list updated.
        const groupedScriptsBlockedState = {
          userInteracted: false,
          willBlock: noScript[url].actuallyBlocked
        }
        state = setNoScriptInfo(state, url, groupedScriptsBlockedState)
      }
      break
  }

  if (state !== startingState) {
    storage.debouncedSave(state)
  }

  // console.log('what goes around comes around', noScript)
  return state
}

export default featureReducer
